@import url(//fonts.googleapis.com/css?family=Google+Sans:300,400,700);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}table{border-collapse:collapse}table,th,td{border:1px solid lightcoral}th,td{padding:1em}*{box-sizing:border-box}#root{padding-top:40px;position:relative;height:100%}::-webkit-scrollbar{width:10px;background:transparent}::-webkit-scrollbar-track{box-shadow:inset 0 0 5px grey;border-radius:10px}::-webkit-scrollbar-thumb{background:#057e33}::-webkit-scrollbar-thumb:hover{background:darkcyan;width:10px}

.nav-bar{position:fixed;top:0;left:0;display:flex;z-index:1000;flex-direction:row;height:40px;background:#333;width:100vw}.nav-bar ul{display:flex;align-items:center}.nav-bar ul li{display:inline;margin-right:20px}.nav-bar ul li:last-child{margin-right:unset}.nav-bar ul a{color:#88BBBB}.with-sidebar{display:flex;flex:1 1;flex-direction:row;height:100%;margin:0px}

.side-bar{position:fixed;left:0px;top:40px;bottom:0px;overflow-y:hidden;overflow-x:hidden;background:#1F3F1F;color:#9C8D9D;width:300px;padding-left:4px;padding-top:20px;padding-bottom:20px;flex:0 0 auto;border-right:2px solid #2F4F2F}.side-bar .item{padding:4px;height:36px;display:flex}.side-bar .item a{color:#9C8D9D;background:0 0;text-decoration:none;text-overflow:ellipsis;white-space:nowrap;overflow:hidden;flex:1 1}.side-bar .item a.active{color:#008CFF}.side-bar .item:hover{color:#008CFF;background:#332C3F}.side-bar .indent-0{margin-top:16px;padding-left:8px}.side-bar .indent-0:first-child{margin-top:0px}.side-bar .indent-1{padding-left:38px}.side-bar .indent-2{padding-left:68px}.side-bar:hover{overflow-y:overlay}

.content-page{margin-left:300px;background:#1F3F1F;color:#7CDDDD;flex:1 1;padding:20px;font-family:Content-font, Roboto, sans-serif;font-weight:500;line-height:1.5;min-height:100vh}.content-page>ul:nth-child(2){padding-bottom:20px;padding-left:20px;background:#22662244;list-style:none;padding-top:20px;border-radius:4px;border:1pt #88AA8844 solid}.content-page>ul:nth-child(2) a{-webkit-text-decoration-line:none;text-decoration-line:none}.content-page .code-block{max-width:1200px}.content-page h1{outline:none;font-size:1.5em;font-family:'Google Sans', Arial, sans-serif;counter-reset:h2 h3 h4 h5;font-weight:normal}.content-page h2{font-size:1.2em;color:lightgreen;font-family:'Google Sans', Arial, sans-serif;counter-reset:h3 h4 h5;font-weight:normal}.content-page h3{font-family:'Google Sans', Arial, sans-serif;font-size:1.1em;color:#9099dd;counter-reset:h4 h5;font-weight:normal;border-top:2px solid #aabbdd11}.content-page h2,.content-page h3,.content-page h4,.content-page h5{padding-top:40px}.content-page .emoji{padding-right:0.5em;font-size:1em}.content-page .active.emoji{color:transparent;text-shadow:0 0 #7F7F7F3F}.content-page .done.emoji{color:transparent;text-shadow:0 0 #33AA33}.content-page li.task{list-style:none;margin-left:-20px}.content-page p code{background:#1F301F}.content-page a:visited,.content-page a:link{color:#77c}

.code-block{position:relative}.code-block .tooltip{position:absolute;display:inline;right:0;top:0;margin-right:40px;padding:7px;background-color:#5cAACC;color:#000000;z-index:201;font-size:0.75em;border-radius:4px}.code-block .tooltip::after{position:absolute;border-top:5px solid transparent;border-bottom:5px solid transparent;border-left:5px solid #5cBBFF;margin:auto auto auto 7px;content:" ";height:0px;font-size:0;top:0;bottom:0;z-index:202}.code-block .tooltip-hidden{display:none}.code-block .codeblock-copy-btn{display:flex;cursor:pointer;background:#3b584e;color:white;text-align:center;outline:none;border:none;border-radius:0.25em;font-size:1.25em;padding:4px 4px 4px 4px;float:right}

