@import "./variables.scss";

.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  z-index: 1000;
  flex-direction: row;
  height: $topNavBarHeight;
  background: $topNavBarBackgroundColor;
  width: 100vw;


  ul {
    display: flex;
    align-items: center;
    li {
      display: inline;
      margin-right: 20px;
    }
    li:last-child {
      margin-right: unset;
    }

    a {
      color: #88BBBB;
    }
  }
}

.with-sidebar {
  display: flex;
  flex: 1;
  flex-direction: row;
  height: 100%;
  margin: 0px;
}
