@import "../variables.scss";

.code-block {
  position: relative;
  .tooltip {
    position: absolute;
    display: inline;
    right: 0;
    top: 0;
    margin-right: 40px;
    padding: 7px;
    background-color: #5cAACC;
    color: #000000;
    z-index: 201;
    font-size: 0.75em;
    border-radius: 4px;
  }

  .tooltip::after {
    position: absolute;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid #5cBBFF;
    margin: auto auto auto 7px;
    content: " ";
    height: 0px;
    font-size: 0;
    top: 0;
    bottom: 0;
    z-index: 202;
  }

  .tooltip-hidden {
    display: none;
  }

  .codeblock-copy-btn {
    display: flex;
    cursor: pointer;
    background: #3b584e;
    color: white;
    text-align: center;
    outline: none;
    border: none;
    border-radius: 0.25em;
    font-size: 1.25em;
    padding: 4px 4px 4px 4px;
    float: right;
  }
}