@import "../variables.scss";
@import url(//fonts.googleapis.com/css?family=Google+Sans:300,400,700);

.content-page {
  margin-left: $leftSideBarWidth;
  background: $contentBackgroundColor;
  color: $contentColor;
  flex: 1;
  padding: 20px;
  font-family: Content-font, Roboto, sans-serif;
  font-weight: 500;
  line-height: 1.5;
  min-height: 100vh;

  >ul:nth-child(2) {
    padding-bottom: 20px;
    padding-left: 20px;
    background: #22662244;
    list-style: none;
    padding-top: 20px;
    border-radius: 4px;
    border: 1pt #88AA8844 solid;

    a {
      text-decoration-line: none;
    }
  }

  .code-block {
    max-width: 1200px;
  }

  h1 {
    outline: none;
    font-size: 1.5em;
    font-family: 'Google Sans', Arial, sans-serif;
    counter-reset: h2 h3 h4 h5;
    font-weight: normal;
  }
  h2 {
    font-size: 1.2em;
    color: lightgreen;
    font-family: 'Google Sans', Arial, sans-serif;
    counter-reset: h3 h4 h5;
    font-weight: normal;
  }

  h3 {
    font-family: 'Google Sans', Arial, sans-serif;
    font-size: 1.1em;
    color: #9099dd;
    counter-reset: h4 h5;
    font-weight: normal;
    border-top: 2px solid #aabbdd11;
  }


  h2, h3, h4, h5 {
    //margin-top: -$topNavBarHeight + 10px;
    padding-top: $topNavBarHeight;
  }

  .emoji {
    padding-right: 0.5em;
    font-size: 1em;
  }
  .active.emoji {
    color: transparent;
    text-shadow: 0 0 #7F7F7F3F;
  }
  .done.emoji {
    color: transparent;
    text-shadow: 0 0 #33AA33;
  }
  li.task {
    list-style: none;
    margin-left: -20px;
  }



  p {
    code {
      background: #1F301F;
    }
  }

  a:visited, a:link {
    color: $contentLinkColor;
  }
}


