@import "../variables.scss";

.side-bar {
  position: fixed;
  left: 0px;
  top: $topNavBarHeight;
  bottom: 0px;
  overflow-y: hidden;
  overflow-x: hidden;
  background: $contentBackgroundColor;
  color: $sideNavColor;
  width: $leftSideBarWidth;
  padding-left: 4px;
  padding-top: 20px;
  padding-bottom: 20px;

  flex: 0 0 auto;
  border-right: 2px solid #2F4F2F;
  .item {
    padding: 4px;
    height: 36px;
    display: flex;
    a {
      color: $sideNavColor;
      background: 0 0;
      text-decoration: none;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      flex: 1;
    }
    a.active {
      color: #008CFF;
    }
  }
  .item:hover {
    color: #008CFF;
    background: #332C3F;
  }

  .indent-0 {
    margin-top: 16px;
    padding-left: 8px;
  }
  .indent-0:first-child {
    margin-top: 0px;
  }

  .indent-1 {
    padding-left: 38px;
  }

  .indent-2 {
    padding-left: 68px;
  }
}


.side-bar:hover {
  overflow-y: overlay;
}